import { addFilter } from '@wordpress/hooks';
import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TextControl } from '@wordpress/components';
import { createHigherOrderComponent } from '@wordpress/compose';

addFilter(
	'blocks.registerBlockType',
	'hd/better-core-video-embeds',
	( settings, name ) => {
		
		if ( name !== 'core/embed' ) {
			return settings;
		}

		return {
			...settings,
			attributes: {
				...settings.attributes,
				thumbnailUrl: {
					type: 'string',
					default: '',
				}
			},
		};

	}
);

function Edit(props) {

	const setThumbnailUrl = (value) => {
		props.setAttributes({ thumbnailUrl: value });
	};

	return (
		<InspectorControls>
			<PanelBody title={__("Video Thumbnail")}>
				<PanelRow>
					<TextControl
						label={__( "Custom Thumbnail URL" )}
						value={props.attributes.thumbnailUrl}
						onChange={setThumbnailUrl}
						help={__("Enter the attachment ID of your custom thumbnail.")}
					/>
				</PanelRow>
			</PanelBody>
		</InspectorControls>
	);
}

/**
 * Add the edit component to the block.
 * This is the component that will be rendered in the editor.
 * It will be rendered after the original block edit component.
 *
 * @param {function} BlockEdit Original component
 * @returns {function} Wrapped component
 *
 * @see https://developer.wordpress.org/block-editor/developers/filters/block-filters/#editor-blockedit
 */
addFilter(
	"editor.BlockEdit",
	"hd/better-core-video-embeds",
	createHigherOrderComponent((BlockEdit) => {
		return (props) => {
			if (props.name !== "core/embed") {
				return <BlockEdit {...props} />;
			}

			return (
				<>
					<BlockEdit {...props} />
					<Edit {...props} />
				</>
			);
		};
	})
);